<template>
   <div class="cms-form-layout">
    <el-tabs v-model="activeName" >
      <el-tab-pane label="价格区间" name="price">
        <cms-prompt>
          <li>价格区间作用于交易分析中的订单金额分布</li>
        </cms-prompt>
        <price></price>
      </el-tab-pane>
      <el-tab-pane label="访问深度" name="depth">
        <cms-prompt style="height:41px;">
          <li>访问深度作用于流量分析中的访问深度</li>
        </cms-prompt>
        <depth></depth>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
// 深度
import depth from './depth'
// 价格
import price from './price'
export default {
  name: 'sectionLayOut',
  components: {
    depth,
    price
  },
  data() {
    return {
      activeName: 'price'
    }
  }
}
</script>

<style>

</style>


