<template>
  <div>
      <section class="cms-batch">
        <cms-button icon="add" type="success" size="small" @click="add('add')">新增</cms-button>
      </section>
    <el-table :data="tableData" stripe  v-loading="loading" @selection-change='getCheckItems' @row-click='checkRows' ref='dataTable'>
      <el-table-column prop="id" label="序号" width="60" align="center"></el-table-column>
      <el-table-column label="区间" min-width="120px" align="center">
        <template slot-scope="scope">
          <span v-if="scope.row.intervalMax">{{scope.row.intervalMin+'/'+scope.row.intervalMax}}</span>
          <span v-else>{{scope.row.intervalMin}}或以上</span>
        </template>
      </el-table-column>
      <el-table-column prop="alias" label="别名" min-width="120px" align="center"></el-table-column>
      <el-table-column prop="sortNum" label="排序值" min-width="120px" align="center"></el-table-column>
      <el-table-column label="操作选项" width='250' fixed="right" align="center">
        <div slot-scope="scope">
          <cms-button icon='edit' size='small' type='info' @click="getData(scope.row.id)">编辑</cms-button>
          <cms-button icon='edit' size='small' type='info' @click="delData(scope.row.id)">删除</cms-button>
        </div>
      </el-table-column>
    </el-table>
    <cms-pagination @change="getPage" :total='total'></cms-pagination>
    <el-dialog :title="titleName" :visible.sync="addShow" width="600px" :before-close="cancel">
      <el-form ref="form" :rules="rules" :model="formData" label-width="90px">
        <el-form-item prop="interval" label="区间:">
          <el-input v-model="formData.interval" maxlength="17"></el-input>
          <div class="gray">格式为：最小值 / 最大值，即大于等于最小值，小于等于最大值，注：最大值可以为空，即表示不限制最大值</div>
        </el-form-item>
        <el-form-item prop="alias" label="别名:">
          <el-input v-model="formData.alias" maxlength="17"></el-input>
          <div class="gray">别名即作用于显示在相应统计图表中的X轴或Y轴</div>
        </el-form-item>
        <el-form-item prop="sortNum" label="排序值:">
          <el-input v-model="formData.sortNum"  maxlength="2"></el-input>
          <div class="gray">排序影响在统计图表中X轴或Y轴的顺序，值越小越靠前</div>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button size="mini" type="primary" @click="subData">确认</el-button>
        <el-button size="mini" @click="cancel">关闭</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import {
  statisticsSetPage,
  statisticsSetGet,
  statisticsSetSave,
  statisticsSetUpdate,
  statisticsSetDel
} from '@/api/statistics/index.js'
import list from '@/mixins/list'
import rules from '@/rules/index'
import form from '@/mixins/form'
var defaultData = {
  size: 10,
  page: 1,
  setupType: 2
}
var defaultFrom = {
  'setupType': 2,
  'interval': '',
  'alias': '',
  'sortNum': ''
}
export default {
  mixins: [list, form],
  data() {
    var validateInterval = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请填写区间'))
      } else {
        if (value.indexOf('/') < 0) {
          callback(new Error('请使用/分隔'))
        } else {
          const reg = /^\d{0,8}$/
          const temp = value.split('/')
          if (temp.length <= 2) { // 限制分隔符只能出现一次
            if (!reg.test(temp[0])) { // 未输入最大值时
              callback(new Error('最小值最大长度为8'))
            } else if (temp.length > 1 && (!reg.test(temp[1]))) {
              callback(new Error('最大值最大长度为8'))
            } else if (parseInt(temp[0]) > parseInt(temp[1])) {
              callback(new Error('最小值不能大于最大值'))
            } else {
              callback()
            }
          } else { // 多于一条时
            callback(new Error('格式不正确'))
          }
        }
      }
    }
    return {
      queryParams: Object.assign({}, defaultData),
      formData: Object.assign({}, defaultFrom),
      addShow: false, // 新增弹框
      titleName: '区间新增',
      rules: {
        'interval': [rules.required('请填写区间'), rules.custom(validateInterval)],
        'alias': [rules.required('请填写区间别名')],
        'sortNum': [rules.required('请输入排序值'), rules.number()]
      }
    }
  },
  watch: {
    'formData.interval': {
      handler(newV) {
        if (newV && newV !== '') {
          if (newV.indexOf('/') !== newV.length - 1) {
            newV = newV.replace('/', '-')
          }
        }
        this.formData.alias = newV
      }
    }
  },
  methods: {
    subData() { // 提交表单
      this.$refs.form.validate(valid => {
        if (valid) {
          if (this.titleName === '区间新增') {
            this.save()
          } else {
            this.update()
          }
        }
      })
    },
    cancel() { // 关闭弹窗
      this.addShow = false
      this.formData = Object.assign({}, defaultData)
      this.$refs.form.clearValidate()
    },
    add(type) { // 打开弹窗
      if (type === 'add') {
        this.titleName = '区间新增'
      } else {
        this.titleName = '区间修改'
      }
      this.addShow = true
    },
    delData(id) { // 删除
      this.deleteTableData(statisticsSetDel, id)
    },
    save() { // 新增
      const params = this.$cms.deepCopy(this.formData)
      statisticsSetSave(params).then(res => {
        if (res.code === 200) {
          this.$successMessage('新增成功')
          this.cancel()
          this.getList()
        } else {
          this.$errorMessage(res.message)
        }
      })
    },
    update() { // 修改
      const params = this.$cms.deepCopy(this.formData)
      statisticsSetUpdate(params).then(res => {
        if (res.code === 200) {
          this.$successMessage('修改成功')
          this.cancel()
          this.getList()
        } else {
          this.$errorMessage(res.message)
        }
      })
    },
    getData(id) { // 获取
      statisticsSetGet({ id: id }).then(res => {
        if (res.code === 200) {
          this.formData = {
            id: res.data.id,
            'setupType': 2,
            'interval': res.data.intervalMax ? res.data.intervalMin + '/' + res.data.intervalMax : res.data.intervalMin + '/',
            'alias': res.data.alias,
            'sortNum': res.data.sortNum
          }
          this.add('update')
        }
      })
    },
    getList() {
      this.getTableData(statisticsSetPage, this.queryParams)
    }
  },
  created() {
    this.getList()
  }
}
</script>




